import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AboutComponent } from './components/about/about.component';
import { ContactComponent } from './components/contact/contact.component';
import { HomeComponent } from './components/home/home.component';
import { LoginComponent } from './components/login/login.component';
import { PageNotFoundComponent } from './components/page-not-found/page-not-found.component';
import { ParentComponent } from './components/parent/parent.component';
import { DesktopsComponent } from './components/products/desktops/desktops.component';
import { LaptopsComponent } from './components/products/laptops/laptops.component';
import { MobilesComponent } from './components/products/mobiles/mobiles.component';
import { ProductsComponent } from './components/products/products.component';
import { ShoesComponent } from './components/products/shoes/shoes.component';


const routes: Routes = [
  { path: '', pathMatch: 'full', redirectTo: 'home'},
  { path: 'login', component: LoginComponent },
  { path: 'home', component: HomeComponent },
  { path: 'about', component: AboutComponent },
  { path: 'products', component: ProductsComponent, children: [
    { path: 'laptops', component: LaptopsComponent },
    { path: 'mobiles', component: MobilesComponent },
    { path: 'desktops', component: DesktopsComponent },
    { path: 'shoes', component: ShoesComponent }
  ] },
  { path: 'contact', component: ContactComponent },
  { path: 'buy', component: ParentComponent },
  { path: '**', component: PageNotFoundComponent }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
